import "bootstrap/dist/css/bootstrap.css";
import { database } from "./config";
import { ref, set } from "firebase/database";
import { useState, Fragment } from "react";
import { Dropdown, Button, Accordion } from "react-bootstrap";
import { BsCheckCircleFill } from "react-icons/bs"

import Finished from "./Finished";

const Roles = ({ discordID, email, name }) => {
  const [pronouns, setPronouns] = useState([]);
  const [academy, setAcademy] = useState("");
  const [grade, setGrade] = useState("");
  const [disabled, setDisabled] = useState(true);
  const [submitted, setSubmitted] = useState(false);

  const validPronouns = [
    "He/Him",
    "She/Her",
    "They/Them",
    "He/Them",
    "She/Them",
    "It/Its",
    "Ask For Pronouns",
  ];

  const validAcademies = [
    "AMES",
    "Computer Science",
    "Medical Assisting",
    "Dental Assisting",
    "Health Sciences",
    "CADD",
    "Graphic Design",
    "Culinary Arts",
    "Cosmetology",
    "Fashion Design",
    "Performing Arts",
    "Aviation Studies",
    "JROTC",
    "Automotive Tech",
  ];

  const validGrades = ["Freshman", "Sophomore", "Junior", "Senior", "Graduate"];

  const handlePronounChange = (ev) => {
    pronouns.indexOf(ev.target.value) === -1
      ? setPronouns([...pronouns, ev.target.value])
      : setPronouns(pronouns.filter((p) => p !== ev.target.value));
  };

  const handleAcademyChange = (ev) => {
    setAcademy(ev.target.value);
    setDisabled(!academy && !grade);
  };

  const handleGradeChange = (ev) => {
    setGrade(ev.target.value);
    setDisabled(!academy && !grade);
  };

  const handleSubmit = (ev) => {
    ev.preventDefault();
    setSubmitted(true);

    set(ref(database, "users/" + discordID), {
      name,
      email,
      pronouns,
      academy,
      grade,
    });
  };

  if (submitted) return <Finished />;

  return (
    <div className="container-fluid w-100">
      <Accordion defaultActiveKey="0" flush className="d-grid gap-2">
        <Accordion.Item eventKey="0">
          <Accordion.Header>Pronouns
            {pronouns.length > 0 ? (<BsCheckCircleFill color="green" className="mx-2"/>) : ""}
            </Accordion.Header>
          <Accordion.Body className="d-grid gap-2">
            {validPronouns.map((pronoun, index) => (
              <Fragment key={"pronoun-" + index}>
                <input
                  type="checkbox"
                  className="btn-check"
                  id={"pronoun-opt-" + index}
                  value={pronoun}
                  onChange={handlePronounChange}
                />
                <label
                  className="btn btn-outline-primary"
                  htmlFor={"pronoun-opt-" + index}>
                  {pronoun}
                </label>
              </Fragment>
            ))}
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="1">
          <Accordion.Header>Academy<span style={{color:"red"}}>*</span>
          {academy != "" ? (<BsCheckCircleFill color="green" className="mx-2"/>) : ""}
          </Accordion.Header>
          <Accordion.Body className="d-grid gap-2">
            {validAcademies.map((validAcademy, index) => (
              <Fragment key={"academy-" + index}>
                <input
                  type="radio"
                  name="academy"
                  className="btn-check"
                  id={"academy-opt-" + index}
                  value={validAcademy}
                  onChange={handleAcademyChange}
                />
                <label
                  className="btn btn-outline-primary"
                  htmlFor={"academy-opt-" + index}>
                  {validAcademy}
                </label>
              </Fragment>
            ))}
          </Accordion.Body>
        </Accordion.Item>
        <Accordion.Item eventKey="2">
          <Accordion.Header>Grade<span style={{color:"red"}}>*</span>
          {grade != "" ? (<BsCheckCircleFill color="green" className="mx-2"/>) : ""}
          </Accordion.Header>
          <Accordion.Body className="d-grid gap-2">
            {validGrades.map((validGrade, index) => (
              <Fragment key={"grade-" + index}>
                <input
                  type="radio"
                  name="grade"
                  className="btn-check"
                  id={"grade-opt-" + index}
                  value={validGrade}
                  onChange={handleGradeChange}
                />
                <label
                  className="btn btn-outline-primary"
                  htmlFor={"grade-opt-" + index}>
                  {validGrade}
                </label>
              </Fragment>
            ))}
          </Accordion.Body>
        </Accordion.Item>
        <Button onClick={handleSubmit} disabled={disabled}>Finish</Button>
      </Accordion>
    </div>
  );
};

export default Roles;
